




body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
/*   -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
  box-sizing: border-box;
  min-width: 1050px;
}
a {
  color: #FFFFFF;
}

button {
  transition: all .2s ease-out;
}


.header {
  position: absolute;
  width: 100%;
  min-width: 1050px;
  z-index: 100;
  line-height: normal;
}

.logo, .nav {
  min-width: 750px;
  margin: 0px auto;
  width: 1050px;

}
.logo {
  padding: 5px 0px;
}

.logo__link {
font-style: normal;
font-weight: 900;
font-size: 36px;
}

.logo__link, .nav__link {
  text-decoration: none;
  padding: 11px 35px 7px;
}

nav {
  background: #292929;
}

.nav {
padding: 20px 0;
box-sizing: border-box;
height: 75px;
list-style: none;
}

.nav__item {
 display: inline-block;
}

.nav__link {
  font-weight: 100;
  font-size: 25px;
}

.motto {
  font-weight: 100;
font-size: 54px;
line-height: normal;
color: #FFFFFF;
padding-bottom: 150px;

}

.motto-bold {
  font-weight: 700; 
}

.main__picker, .main__motto {
  margin: 0 auto;
}



.main {
  width: 100%;
  display: -webkit-flex;
  display: flex;
-webkit-justify-content: center;
        justify-content: center;
-webkit-align-items: flex-end;
        align-items: flex-end;
  position: relative;
  background-image: url(/static/media/main.6e90bf14.png);
  background-repeat: no-repeat;
  background-size: cover;
  height: 700px;
}

.main::after {
  content: "";
  width: 100%;
  height: 8px;
  display: block;
  position: absolute;
  bottom: -8px;
  background-color:#FFA800;
}


.trainpicker {
  background: rgba(41, 41, 41, 0.8);
  width: 640px;
  height: 450px;
  color: #FFFFFF;
  font-weight: 300;
  padding: 60px 20px;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  box-sizing: border-box;
}
.trainpicker__content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.trainpicker__label {
  font-size: 26px;
  display: block;
   margin-bottom: 10px;
}
.trainpicker__inputs {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}
.trainpicker__input {
  box-sizing: border-box;
  font-size: 14px;
  font-weight: 100;
  padding-left: 10px;
  height: 50px;
  width: 285px;
  border: 0;
  outline: none;
  border-radius: 3px;
}
.input-with-icon {
  position: relative;
  display: inline-block;
}
.trainpicker__icon {
  color: #e5e5e5;
  position: absolute;
  right: 6px;
  top: 6px;
  font-size: 30px;
}
.trainpicker__reverse {
  -webkit-appearance: none;
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
  width: 20px;
  height: 20px;
  cursor: pointer;
  background-image: url(/static/media/round-cached-24px.87855d0a.svg);
  background-size: contain;
  transition: all .35s ease-out;
}
.trainpicker__reverse:hover {
-webkit-transform: scale(1.2);
        transform: scale(1.2);
}
.trainpicker__button {
  background: #FFA800;
  border: 1px solid  #FFA800;
border-radius: 5px;
width: 285px;
height: 50px;
font-size: 20px;
font-weight: 500;
line-height: normal;
text-align: center;
text-transform: uppercase;
color: #292929;
-webkit-align-self: flex-end;
        align-self: flex-end;
cursor: pointer;
margin-top: 40px;
}
.trainpicker__button:hover {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.trainpicker__button:active {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  background-color: transparent;
  color: #FFA800;
}
[type="date"]::-webkit-inner-spin-button {
  display: none;
}
[type="date"]::-webkit-calendar-picker-indicator {
  opacity: 0;
}


